import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Card,
  Form,
  DatePicker,
  Button,
  Radio,
  Select,
  Empty,
} from "antd";
import { Table, Space } from "antd";
import { connect } from "react-redux";
import moment from "moment";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import EmptyImage from "../../../../assets/images/app/empty-list.svg";
import {
  InputChangeValue,
  getReportList,
  clearData,
} from "../../../../store/reports/reportsSlice";
import {
  getMiniCounters,
  getMiniLeaveReason,
} from "../../../../store/mini/miniSlice";
import {
  getUsers,
  setUsersParams,
} from "../../../../store/settings/ManageUsers/manageUserSlice";

function LeaveReport(props) {
  const [form] = Form.useForm();
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  useEffect(() => {
    props.clearData();
    props.getMiniCounters();
    props.getMiniLeaveReason();
    props.getUsers();
  }, []);

 

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {
    if (props.reportParamsData.file_format == "HRMS") {
      var { file_format, ...params } = props.reportParamsData;
      var objData = {
        file_format: 2,
        model_name: "HRMS Enquiry",
      };
    } else {
      var { file_format, ...params } = props.reportParamsData;
      var objData = {
        file_format: file_format,
        model_name: "Leave",
      };
    }
    props.getReportList({
      objData,
      ParamsData: {
        ...params,
        start_date:
          params.start_date != null
            ? moment(params.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          params.end_date != null
            ? moment(params.end_date).add(1, "days").format("YYYY-MM-DD")
            : "",
      },
    });
  };
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  const columns = props.columnsList.map((column) => {
    return {
      title: column,
      dataIndex: column,
    };
  });
  columns.unshift({ title: "S.No", dataIndex: "index" });

  const data = props.reportDataList.map((row, key) => ({
    ...row,
    index: key + 1,
  }));

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.reportParamsData?.start_date,
      end_date: props.reportParamsData?.end_date,
      file_format: props.reportParamsData?.file_format,
      counter: props.reportParamsData?.counter,
      user_id: props.reportParamsData?.user_id,
      leave: props.reportParamsData?.leave,
    });
  }, [props.reportParamsData]);
  return (
    <Row gutter={[12, 12]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Leave Report"
        // addModelTile="Add Zone"
        goBack={true}
        // setIsModalVisible={props.isModelVisible}
        // isModalVisible={props.model}
      />
      <Col span={24}>
        <Card>
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="From Date"
                  name="start_date"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your From Date!",
                    },
                  ]}
                >
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment(),
                    })}
                    onChange={(date) => {
                      props.InputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.InputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="To Date"
                  name="end_date"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your To Date!",
                    },
                  ]}
                >
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.reportParamsData?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.reportParamsData.start_date == null ||
                      props.reportParamsData.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.InputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item label="Counter :" name="counter">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Counter"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "counter",
                        value: value,
                      });
                    }}
                    value={
                      props.reportParamsData.counter_id == null
                        ? ""
                        : props.reportParamsData?.counter_id
                    }
                  >
                    {props.miniCountersList.map((bank, index) => {
                      return (
                        <Option key={index} value={bank.id}>
                          {bank.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="User :"
                  name="user_id"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select User",
                  //   },
                  // ]}
                  hasFeedback
                >
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select User"
                    optionFilterProp="User"
                    // disabled
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onSearch={(value) => {
                      props.setUsersParams({
                        ...props.usersParams,
                        page: 1,
                        search: value,
                      });
                      props.getUsers({
                        ...props.usersParams,
                        page: 1,
                        search: value,
                      });
                    }}
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "user_id",
                        value: value,
                      });
                    }}
                    onPopupScroll={() => {
                      if (
                        props.usersParams.page < props.usersParams.no_of_pages
                      ) {
                        props.getUsers({
                          ...props.usersParams,
                          page: props.usersParams.page + 1,
                        });
                        props.setUsersParams({
                          ...props.usersParams,
                          page: props.usersParams.page + 1,
                        });
                      }
                    }}
                    value={
                      props.reportParamsData.user_id == null
                        ? ""
                        : props.reportParamsData?.user_id
                    }
                  >
                    {props.Userslist.map((empUser, index) => {
                      return (
                        <Option key={index} value={empUser.user.id}>
                          {empUser.user.username}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item label="Leave Type :" name="leave">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Leave"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "leave",
                        value: value,
                      });
                    }}
                    value={
                      props.reportParamsData.leave_id == null
                        ? ""
                        : props.reportParamsData?.leave_id
                    }
                  >
                    {props.miniLeaveReasonList.map((leave, index) => {
                      return (
                        <Option key={index} value={leave.id}>
                          {leave.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={10} xl={10}>
                <Form.Item
                  label="Report Format"
                  name="file_format"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your Report Format!",
                    },
                  ]}
                >
                  <Radio.Group
                    name="file_format"
                    onChange={ChangeHandeler}
                    value={props.reportParamsData?.file_format}
                  >
                    <Radio.Button value={0}>CSV</Radio.Button>
                    <Radio.Button value={2}>XLSX</Radio.Button>
                    {/* <Radio.Button value={"HRMS"}>HRMS</Radio.Button> */}
                    <Radio.Button value={5}>VIEW</Radio.Button>
                  </Radio.Group>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12} offset={12}>
                <Button
                  // block
                  type="secondary"
                  className="hp-submit-btn hp-float-right hp-mt-10 hp-ml-10"
                  onClick={() => {
                    props.clearData();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
                <Button
                  // block
                  type="primary"
                  className="hp-submit-btn hp-float-right hp-mt-10"
                  disabled={props.loading}
                  loading={props.loading}
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Generate Report
                </Button>
              </Col>
            </Row>
          </Form>
        </Card>
      </Col>

      <Col span={24}>
      {props.reportView && (
          <>
        {props.reportDataList.length != 0 &&
        props.reportParamsData.file_format == 5 ? (
          <>
            <Card>
              <Row className="hp-mb-12">
                <Col span={24}>
                  <Table
                    columns={columns}
                    dataSource={data}
                    scroll={{ x: 500 }}
                    pagination={false}
                    size="middle"
                    loading={props.loading}
                  />
                </Col>
              </Row>
            </Card>
          </>
        ) : (
          <Card>
            <Empty
              className="hp-m-32"
              image={EmptyImage}
              imageStyle={{
                height: 160,
              }}
              description={<h5>Your Leave Report is empty</h5>}
            ></Empty>
          </Card>
        )}
        </>
        )}
      </Col>
    </Row>
  );
}

function mapStateToProps({ reports, mini, manageUsers }) {
  return {
    modelName: reports.modelName,
    reportParamsData: reports.reportParamsData,
    reportDataList: reports.reportDataList,
    loading: reports.loading,
    columnsList: reports.columnsList,
    miniCountersList: mini.miniCountersList,
    miniLeaveReasonList: mini.miniLeaveReasonList,
    Userslist: manageUsers.Userslist,
    usersParams: manageUsers.usersParams,
    reportView: reports.reportView
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  getReportList,
  clearData,
  getMiniCounters,
  getMiniLeaveReason,
  getUsers,
  setUsersParams,
})(LeaveReport);
